.progress-container {
  height: .3rem;
  margin-bottom: 5px;
  background-color: #f4f4f4;
}

.progress-container .progress {
  height: 100%;
  background-color: #33cc3b;
  -webkit-transition: width ease-in-out 0.4s;
  -o-transition: width ease-in-out 0.4s;
  transition: width ease-in-out 0.4s;
}


