.done-box-container {
  position: absolute;
  left: 50%;
  top: 50%;
  right: 50%;
  bottom: 50%;
  z-index: 10;
  -webkit-transition: all ease-out 1s;
  -o-transition: all ease-out 1s;
  transition: all ease-out 1s;
}

.done-box-container.in {
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  padding: 2rem;
}

.done-box-container .done-box {
  width: 100%;
  height: 100%;
  background: rgba(255,255,255,0.9);
  font-size: .5rem;
  line-height: .5rem;
  overflow: hidden;
  white-space: nowrap;
  -o-text-overflow: clip;
     text-overflow: clip;
  opacity: 0;
  -webkit-transition: all ease-out 1s;
  -o-transition: all ease-out 1s;
  transition: all ease-out 1s;
}

.done-box-container.in .done-box {
  padding: 1rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
  opacity: 1;
}

.done-box-container .done-box .done-box-inner {
  position: relative;
}

.done-box-container .done-box .done-box-inner .close-icon {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 1rem;
  opacity: .5;
  -webkit-transition: opacity .5s;
  -o-transition: opacity .5s;
  transition: opacity .5s;
}

.done-box-container .done-box .done-box-inner .close-icon:hover {
  opacity: 1;
  cursor: pointer;
}


