.card-container {
  display: inline-block;
  padding: .2rem;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  vertical-align: top;
}

.card {
  position: relative;
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  -webkit-transition: -webkit-transform, -webkit-box-shadow;
  transition: -webkit-transform, -webkit-box-shadow;
  -o-transition: transform, box-shadow;
  transition: transform, box-shadow;
  transition: transform, box-shadow, -webkit-transform, -webkit-box-shadow;
  -webkit-transition-duration: .5s;
       -o-transition-duration: .5s;
          transition-duration: .5s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.card .front, .card .back {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.card .front {
  display: inline-table;
  font-size: 2.5rem;
  background: #fff;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  -webkit-transition: color .5s ease-in-out;
  -o-transition: color .5s ease-in-out;
  transition: color .5s ease-in-out;
}

.card .back {
  z-index: 2;
  /* for firefox 31 */
  -webkit-transform: rotateY(0deg);
          transform: rotateY(0deg);
  background: #36c;
  background: 
    -webkit-linear-gradient(335deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    -webkit-linear-gradient(205deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    -webkit-linear-gradient(335deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
    -webkit-linear-gradient(205deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px, #36c;
  background: 
    -o-linear-gradient(335deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    -o-linear-gradient(205deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    -o-linear-gradient(335deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
    -o-linear-gradient(205deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px, #36c;
  background: 
    linear-gradient(115deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    linear-gradient(245deg, transparent 75%, rgba(255,255,255,.8) 75%) 0 0,
    linear-gradient(115deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px,
    linear-gradient(245deg, transparent 75%, rgba(255,255,255,.8) 75%) 7px -15px, #36c;
  background-size: 15px 30px;
  cursor: pointer;
}

.card.locked .back {
  cursor: not-allowed;
}

.card.open, .card.done {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.card:hover, .card.open {
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.card.done {
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
          box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
}

.card.done .front {
  //background: #cfc;
  //color: #3dd28d;
  color: #ccc;
}

.card .front .icon {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}


